@import '../variables.scss';

@media (max-width: 1120px) {
  .calc {
    &__block {
      &__item {
        &-cost {
          width: 320px;
        }
      }

      input[type=range] {
        width: 285px;
      }
    }

    &__contract {
      &_block {
        min-width: 229px;
      }

      button {
        width: 320px;
      }
    }

    &__contract {
      flex-wrap: wrap;
    }
  }
}

@media (max-width: 1080px) {
  .calc {
    &__block {
      &__item {
        &-cost {
          width: 280px;
        }

        input[type=range] {
          width: 245px;
        }
      }
    }
  }
}

@media (max-width: 960px) {
  .calc {
    &__title {
      margin-left: 37px;
      height: 118px;
    }

    &__block {
      flex-direction: column;

      &__item {
        &-cost {
          width: 700px;
        }

        input[type=range] {
          width: 650px;
        }
      }

    }

    &__contract {
      flex-wrap: wrap;
      margin: 45px 50px 0 50px;
    }
  }
}

@media (max-width: 768px) {
  .container {
    margin: 64px auto;
    max-width: 625px;
  }

  .calc {
    &__block {
      &__item {
        &-cost {
          width: 696px;
        }
      }

      input[type=range] {
        width: 650px;
      }
    }
    &__contract {
      button {
        &:hover {
          background: $base-color;
        }
      }
    }
  }
}

@media (max-width: 730px) {
  .container {
    max-width: 600px;
  }

  .calc {
    &__title {
      width: 500px;
      font-size: 40px;
    }

    &__block {
      &__item {
        width: 555px;

        &-cost {
          width: 550px;
        }
      }

      input[type=range] {
        width: 455px;
      }
    }
  }
}

@media (max-width: 600px) {
  .container {
    max-width: 500px;
  }

  .calc {
    &__title {
      width: 420px;
      font-size: 34px;
    }

    &__block {
      &__item {
        width: 500px;

        &-cost {
          width: 495px;
        }
      }

      input[type=range] {
        width: 450px;
      }
    }
  }
}

@media (max-width: 505px) {
  .container {
    max-width: 500px;
    padding: 0 44px;
    //margin: 44px 20px 64px 20px;
    margin: 45px 50px 64px 50px;
  }

  .calc {
    &__title {
      margin-left: 0;
      margin-top: 0;
      //width: 242px;
      //height: 155px
      width: 300px;
      height: 155px;
      text-align: center;
    }

    h2 {
      font-size: 16px;
    }

    &__block {
      &__item {
        width: 280px;

        &-cost {
          width: 280px;
        }

        &-percentInput {
          width: 25px;
          font-size: 22px;
        }

        &-wideInput {
          font-size: 22px;
        }

        div {
          font-size: 22px;
        }
      }

      input[type=range] {
        width: 235px;
      }
    }

    &__contract {
      //margin: 16px 0 0 0;
      margin: 16px 0 0 40px;

      &_block {
        width: 167px;
        height: 91px;

        &-price {
          font-size: 22px;
        }
      }

      button {
        margin-top: 0;
        width: 280px;
        height: 60px;
        font-size: 19px;
      }
    }
  }
}

@media (max-width: 420px) {
  .calc {
    &__title {
      margin-left: -40px;
      width: 242px;
      height: 155px;
      text-align: left;
    }

    &__contract {
      width:280px;
      margin: 16px 0 0 -40px;
    }
  }
}

@media (max-width: 340px) {
  .calc {
    &__title {
      margin-left: -70px;
      width: 242px;
      height: 155px;
      text-align: left;
    }

    &__contract {
      margin: 16px 0 0 -70px;
    }
  }
}